      //     this.searchBarFixed = false;
      //     this.hshow = true;
      //   }
      // } else if (e.deltaY > 0) {
      //   //向下滚动事件
      //   this.searchBarFixed = false;
      //   this.hshow = false;
      // }
    }

    // getCategory: function(params) {
    //   // console.log(e.currentTarget)
    //   let v = this;
    //   if (!params) params = {};
    //   // 使用全局绑定的$api方法来获取数据
    //   v.$api.get("get", { list: this.$route.params.tag?this.$route.params.tag:'hot', p: this.page }, function(r) {
