export function goodTime (input) {
  var now = new Date().getTime()
  var oldTime = input+'000'

  // var y = oldTime.getFullYear();
  // var m = oldTime.getMonth() + 1;
  // var d = oldTime.getDate();
  // return y + "-" + (m < 10 ? "0" + m : m) + "-" + (d < 10 ? "0" + d : d) + " " + oldTime.toTimeString().substr(0, 8);

  var difference = now - oldTime;
  var result = '';
  var minute = 1000 * 60;
  var hour = minute * 60;
  var day = hour * 24;
  var halfamonth = day * 15;
  var month = day * 30;
  var year = month * 12;

  var _year = difference / year;
  var _month = difference / month;
  var _week = difference / (7 * day);
  var _day = difference / day;
  var _hour = difference / hour;
  var _min = difference / minute;
  if (_year >= 1) { result = "发表于 " + ~~(_year) + " 年前" }
  else if (_month >= 1) { result = "发表于 " + ~~(_month) + " 个月前" }
  else if (_week >= 1) { result = "发表于 " + ~~(_week) + " 周前" }
  else if (_day >= 1) { result = "发表于 " + ~~(_day) + " 天前" }
  else if (_hour >= 1) { result = "发表于 " + ~~(_hour) + " 个小时前" }
  else if (_min >= 1) { result = "发表于 " + ~~(_min) + " 分钟前" }
  else result = '刚刚'
  return result
}




